  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lexend", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.seccion {
  padding: 2rem 0;
}

label, input, textarea {
  font-size: 1.8rem !important;
}

input[type=checkbox] {
  transform: scale(1.5) !important;
}

input {
  border: 1px solid #686868;
  border-radius: 0.5rem !important;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

a {
  text-decoration: none;
}

[class$=__heading] {
  margin: 2rem 0;
}

.btn {
  font-size: 2rem !important;
  font-weight: 700 !important;
  padding: 1rem !important;
  width: 100% !important;
  margin: 1rem 0 !important;
  border-radius: 1rem !important;
}

.btn-primary {
  background-color: #eb8129 !important;
  border-color: #eb8129 !important;
}

.btn-light {
  background-color: whitesmoke !important;
  border-color: #eeeeee !important;
}

.btn-responsive {
  margin-left: auto !important;
  margin-right: 1rem !important;
  width: 100% !important;
}
@media (min-width: 768px) {
  .btn-responsive {
    width: 20rem !important;
  }
}

.btn-mini {
  width: 20rem !important;
}

.card-title {
  font-size: 3rem !important;
  text-align: center;
  padding-top: 1rem !important;
}
@media (min-width: 768px) {
  .card-title {
    font-size: 2.3rem !important;
  }
}

.img-responsive {
  min-height: 20rem !important;
  object-fit: cover;
}

.modal-header .modal-title {
  font-size: 2.5rem !important;
}
.modal-header .close {
  font-size: 4rem !important;
}

.header {
  box-shadow: 2px 2px 10px 2px rgba(0, 0, 0, 0.2);
}
.header__contenedor {
  padding: 2rem 1rem;
}
@media (min-width: 768px) {
  .header__contenedor {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
    padding: 1rem 2rem;
  }
}
.header__contenido {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .header__contenido {
    flex-direction: row;
    text-align: left;
    margin: 0;
  }
}
.header__logo {
  width: 20rem;
}
@media (min-width: 768px) {
  .header__logo {
    width: 10rem;
    margin-right: 2rem;
  }
}
.header__titulo {
  font-size: 3rem;
  max-width: 30rem;
}

.navegacion {
  font-size: 2rem;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
  }
}
.navegacion__link {
  display: block;
  font-size: 2rem;
  text-align: center;
  margin-bottom: 2rem;
  border-radius: 2rem;
  padding: 1rem;
}
@media (min-width: 768px) {
  .navegacion__link {
    margin: 0;
    border-radius: 1rem;
  }
}
.navegacion__link:hover {
  background-color: #e2e2e2;
  text-decoration: none;
  font-weight: 700;
}
@media (min-width: 768px) {
  .navegacion__link:hover {
    font-weight: 400;
  }
}
.navegacion__link--primario {
  background: #eb8129;
  color: #fff;
  font-weight: 900;
}
.navegacion__link--primario:hover {
  background: #1b2d2a;
  color: #fff;
  font-weight: 900;
}
.navegacion__link--secundario {
  background: #6e8898;
  color: #fff;
  font-weight: 900;
}
.navegacion__link--secundario:hover {
  background: #1b2d2a;
  color: #fff;
  font-weight: 900;
}

.seccion-publicaciones,
.seccion-actividades {
  padding: 5rem 3rem;
  font-size: 3rem;
}
.seccion-publicaciones__titulo,
.seccion-actividades__titulo {
  font-size: 3rem;
}

.publicacion {
  border-bottom: 1px solid #e1e1e1;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .publicacion {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding-bottom: 2rem;
    gap: 2rem;
  }
  .publicacion__imagen {
    width: 100%;
    max-height: 30rem;
    object-fit: cover;
  }
}
@media (min-width: 1024px) {
  .publicacion--actividad {
    grid-template-columns: none;
    grid-template-rows: 10rem, 20rem;
    padding-bottom: 2rem;
    gap: 2rem;
  }
  .publicacion__imagen--actividad {
    height: auto;
    object-fit: cover;
  }
}
.publicacion__titulo {
  font-size: 3rem;
}
.publicacion__resumen {
  font-size: 2rem;
}
.publicacion__meta {
  display: flex;
  justify-content: space-between;
}
@media (min-width: 1024px) {
  .publicacion__meta {
    flex-direction: column;
  }
}
.publicacion__meta--completa {
  flex-direction: row;
  justify-content: space-between;
}
.publicacion__fecha, .publicacion__escritor {
  font-size: 1.5rem;
}
.publicacion__fecha span, .publicacion__escritor span {
  color: #eb8129;
}

.publicacion--mini {
  margin: 0 auto;
  max-width: 30rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0;
}
.publicacion__imagen--mini {
  width: 100%;
  max-height: 15rem;
}
.publicacion__titulo--mini {
  margin: 0;
}
.publicacion__resumen--mini {
  margin: 0;
}
.publicacion__meta--mini {
  flex-direction: row;
}
.publicacion__meta--mini * {
  margin: 0;
}

.seccion-principal {
  min-height: 70vh;
}
@media (min-width: 1024px) {
  .seccion-principal-contenedor {
    display: grid;
    grid-template-columns: 3fr 1fr;
  }
}

.contenedor-contenido {
  margin: 0 2rem;
}
@media (min-width: 1024px) {
  .contenedor-contenido {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }
}

.publicacion-completa {
  grid-column: 2/4;
  margin: 0 auto;
  max-width: 90rem;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .publicacion-completa {
    padding: 2rem 5rem;
  }
}
.publicacion-completa h2 {
  margin: 2rem 0;
}
.publicacion-completa img {
  margin: 0 auto;
  width: 100%;
  max-height: 40rem;
}

.lateral-contenedor {
  padding: 4rem 0;
}
@media (min-width: 1024px) {
  .lateral-contenedor {
    display: block;
  }
}
.lateral-contenedor aside {
  display: none;
}
@media (min-width: 1024px) {
  .lateral-contenedor aside {
    display: block;
  }
}

.actividad {
  border: 3px solid #e1e1e1;
  border-radius: 1rem;
  padding: 2rem;
  margin-bottom: 2rem;
}
.actividad__texto {
  text-align: center;
}

.seccion-administracion {
  min-height: 70vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.table th {
  font-size: 1.8rem !important;
  text-align: center !important;
}
.table tbody th {
  font-weight: bold;
  color: #fff;
  background-color: #2c3e50;
  border-color: #ddd;
  text-align: center !important;
  vertical-align: middle !important;
}
.table td {
  text-align: center;
  font-size: 1.8rem !important;
  vertical-align: middle !important;
}

.tr-usuarios th:nth-child(4),
.tr-usuarios td:nth-child(4),
.tr-usuarios th:nth-child(2),
.tr-usuarios td:nth-child(2) {
  display: none !important;
}
@media (min-width: 768px) {
  .tr-usuarios th:nth-child(4),
.tr-usuarios td:nth-child(4),
.tr-usuarios th:nth-child(2),
.tr-usuarios td:nth-child(2) {
    display: table-cell !important;
  }
}

.tr-publicaciones th:nth-child(4),
.tr-publicaciones td:nth-child(4) {
  display: none !important;
}
@media (min-width: 768px) {
  .tr-publicaciones th:nth-child(4),
.tr-publicaciones td:nth-child(4) {
    display: table-cell !important;
  }
}

.table-responsive {
  overflow-x: hidden !important;
}

.seccion-perfil {
  min-height: 70vh;
}

.form-group-time {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.form-group--horizontal {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}

.seccion-login {
  min-height: 70vh;
}
@media (min-width: 768px) {
  .seccion-login {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}

.formulario {
  display: block;
  max-width: 45rem;
  padding: 5rem;
  margin: 10rem auto;
  border-radius: 1rem !important;
  box-shadow: 2px 2px 20px 2px rgba(0, 0, 0, 0.2);
}
.formulario label {
  font-weight: 400;
}
.formulario .campo {
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .formulario .campo {
    display: flex;
    align-items: center;
  }
  .formulario label {
    width: 20rem;
  }
}
.formulario h2 {
  text-align: center;
  margin-bottom: 3rem;
  font-weight: 700;
  font-size: 2.5rem;
  color: #eb8129;
  text-transform: uppercase;
}
.formulario input {
  width: 100%;
  padding: 0.5rem;
}
.formulario input[type=submit] {
  display: block;
  margin: 0 auto;
  width: 20rem;
}
.formulario .enlace-cambiar-contrasena {
  display: block;
  font-size: 1.5rem;
  text-align: center;
  margin-top: 2rem;
}
.formulario__texto {
  font-size: 1.8rem;
  color: #686868;
  text-align: center;
}

.seccion-mensaje {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.mensaje__contenedor {
  padding: 5rem;
  background-color: #6e8898;
  border-radius: 1rem;
  margin: 8rem 4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .mensaje__contenedor {
    flex-direction: row;
  }
}
.mensaje__texto {
  font-size: 2.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  width: 100%;
}
@media (min-width: 768px) {
  .mensaje__texto {
    margin-left: 5rem;
  }
}
.mensaje__imagen-contenedor {
  background-color: #FFFFFF;
  padding: 2rem;
  width: 100%;
  max-width: 40rem;
  border-radius: 100%;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .mensaje__imagen-contenedor {
    width: 100%;
    max-width: 20rem;
    margin-bottom: 0;
  }
}
@media (min-width: 1024px) {
  .mensaje__imagen-contenedor {
    width: 50%;
    max-width: 30rem;
  }
}

.footer {
  background: #eb8129;
  padding: 5rem 0;
}
.footer__empresa {
  font-weight: 700;
  font-size: 2rem;
  text-align: center;
  color: white;
  text-transform: uppercase;
  font-size: 3rem;
}

.copyright {
  font-weight: 700;
  font-size: 2rem;
  text-align: center;
  color: white;
}